<template>
    <div class="house">
        <div class="title-content clearfix">
            <div class="title-box fl">
                <div class="t-text">房产>>></div>
            </div>
            <div class="line-box fl"></div>
            <div class="line-box fl"></div>
            <div class="ad-img-box fr">
                <a target="_blank" href="https://gz.fang.anjuke.com/loupan/461934.html?from=loupanlist_sslx&ifxc=935">
                    <img class="ad-img auto-img" src="https://pic4.ajkimg.com/display/xinfang/767cc27e97f2b68046be7f97e499efda/1180x160n.jpg" border="0"  alt="">
                </a>
            </div>
        </div>
        <!-- <div class="banner-content clearfix">
            <div class="banner-box fl">
                <div class="block">
                    <el-carousel height="400px">
                    <el-carousel-item v-for="(item,index) in HouseData.slice(0,6)" :key="index" >
                        <img class="imgs auto-img" :src="item.images" alt="" @click="goDetail(item.id)">
                    </el-carousel-item>
                    </el-carousel>
                </div>
            </div>
            <div class="cart-box fl ">
                <div class="cart-item" v-for="(item,index) in HouseData.slice(8,10)" :key="index" @click="goDetail(item.id)">
                    <img class="imgs" :src="item.images" alt="">
                    <div class="text">{{item.title}}</div>
                </div>
            </div>
        </div> -->
        <div class="newList-content">
            <div class="clearfix">
                <div class="new-list-left-box fl">
                     <div class="list-item clearfix" v-for="(item,index) in HouseData" :key="index" @click="goDetail(item.id)">
                        <div class="left-img fl">
                            <el-image class=" err-imgs">
                                <div slot="error" class="image-slot">
                                    <i class="el-icon-picture-outline"></i>
                                </div>
                            </el-image>
                            <img class="a-img auto-img" :src="item.images" alt="">
                        </div>
                        <div class="right-cont fl">
                            <div class="title">{{item.title}}</div>
                            <div class="text-cont">
                                <span class="post-name">{{item.posterNickName}}</span>
                                <span class="pub-time">日期：{{item.publishTime}}</span>
                                <span class="comment-count">评论：{{item.commentCount}}</span>
                            </div>
                        </div>
                    </div>

                     <div class="pagination-page">
                        <el-pagination 
                        class="pag-box" 
                        background 
                        layout="prev, pager, next" 
                        :size-change="10"
                        :total="1000"
                        :current-change = "currentPage"
                        @current-change="handleCurrentChange"
                        >
                        </el-pagination>
                    </div>
                   
                </div>

                <div class="new-other-right-box fl">
                    <div class="other-list">
                        <div class="other-box-one">
                           <el-carousel height="200px">
                                <el-carousel-item v-for="(item,index) in houseAdData" :key="index" >
                                    <img class="imgs auto-img" :src="item" alt="">
                                </el-carousel-item>
                            </el-carousel>
                        </div>
                        <div class="other-box-two">
                            <div class="car-item">
                                <a href="#" target="_blank" >
                                    <img class="auto-img" src="https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=2745209393,3964229516&fm=26&gp=0.jpg" alt="">
                                </a>
                                <div class="text">广告</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import "../../assets/css/house.less";
    export default {
        name:'house',
        data(){
            return{
                currentPage:1,
                // 定义页数
                pageNum:'',
                HouseData:[],
                houseAdData:[
                    'https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=3829248198,2909032833&fm=26&gp=0.jpg',
                    'https://ss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=3132597517,2747151375&fm=26&gp=0.jpg',
                    'https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=309294174,3645603658&fm=26&gp=0.jpg',
                ],
            }
        },
        created(){
            this.getHouseData()
        },
        methods:{

            getHouseData(pageNum){
                this.axios({
                    method:'GET',
                    url:'/list',
                    params:{
                        apikey: this.appkey,
                        category:'T1348654085632',
                        page:pageNum,
                    }
                }).then((result) =>{
                    // console.log('result',result)
                    this.HouseData = result.data.data
                }).catch((err) =>{
                    // console.log('err',err)
                })
            },
             //点击页码，获取对应的val值，在传给要显示的页数内容
            handleCurrentChange(val){
                
                // console.log('val',val)
                let pageNum = val;
                // console.log('pageNum',pageNum)
                //调用请求数据，才能获取值
                this.getHouseData(pageNum)
            
            },
             goDetail(id) {
                // console.log(id);
                this.$router.push({ name: "Detail", query: { id } });
            },
        }
    }
</script>

